  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

body > .button--start, body > .button--stop {
  color: #00008b;
  border: solid #000;
  border-radius: 3px;
  margin-left: 45%;
  margin-right: 45%;
}

.field {
